//        
export default function convertToDate(timestamp: number) {
    const date = new Date(timestamp);

    let trade_time = new Intl.DateTimeFormat('ko-KR', {
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        hour12: false, // 24 
    }).format(date);
    trade_time = trade_time.replace(". ", "/").replace(".", "").replace(" ", "").replace(" ", "")

    return trade_time;
}