//     ,  
export default function adjustSize(size: number) {
    let str_size;

    // 14   , 14   
    if (size > 9999999999999) {
        str_size = String(Math.floor(size));
    }
    //   14     
    else {
        str_size = String(size);
        str_size = str_size.substring(0, 14);
    }

    //   '.'  . 
    if (str_size.endsWith('.')) {
        str_size = str_size.slice(0, -1);
    }

    return str_size;
}