//      input  
export default function adjustInputValue(value: string) {
    // 00, 01, 02, ...    0    ,   0  
    if (value[0] === '0' && value.length > 1) {
        if (value[1] === '0' || (value[1] >= '1' && value[1] <= '9')) {
            value = value.substring(1);
        }
    }

    // 0..2, 0..4, ...  "."      
    value = value.replace(/(\..*)\./g, "$1");

    //  "."   
    value = value.replace(/[^0-9.]/g, "");

    // "."     
    if (value[0] === '.') {
        value = '0' + value;
    }

    // value   '0' (NaN )
    if (value === '') {
        value = '0';
    }

    return value;
}