import React, { ReactNode, useState } from 'react';
import { Scrollbars } from 'react-custom-scrollbars-2';

type CustomScrollbarsProps = {
  children: ReactNode;
  style?: React.CSSProperties;
  hideScrollBar?: boolean;
  [key: string]: any;
};

const CustomScrollbars: React.FC<CustomScrollbarsProps> = ({ children, hideScrollBar = false, ...props }) => {
  const [isHovered, setIsHovered] = useState(false);

  const renderThumb = ({ style, ...props }: { style: React.CSSProperties }) => {
    const thumbStyle = {
      backgroundColor: '#727272',
      borderRadius: '4px',
      width: '5px', //   
      height: '30px', //   
      marginLeft: '1px',
      opacity: hideScrollBar || !isHovered ? 0 : 1, //      
      transition: 'opacity 0.3s ease', //   
    };
    return <div style={{ ...style, ...thumbStyle }} {...props} />;
  };

  const handleScroll = () => {
    setIsHovered(true);
    setTimeout(() => {
      setIsHovered(false);
    }, 1000); //   1   
  };

  return (
    <div
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      onScroll={handleScroll}
      style={{ height: '100%' }}>
      <Scrollbars
        renderThumbVertical={renderThumb}
        {...props}>
        {children}
      </Scrollbars>
    </div>
  );
};

export default CustomScrollbars;
